import { render } from '@testing-library/react-native';
import ButtonSpinner from '../../../../src/components/ui/Button/ButtonSpinner';
import { ActivityIndicator } from 'react-native';

jest.mock('react-native', () => ({
  ...jest.requireActual('react-native'),
  ActivityIndicator: jest.fn(() => null),
}));

describe('ButtonSpinner', () => {
  beforeEach(() => {
    // RÃ©initialise le mock avant chaque test
    ActivityIndicator.mockClear(); 
  });

  it('devrait rendre un ActivityIndicator avec les props size et color', () => {
    const mockSize = 'large';
    const mockColor = '#FF0000';
    render(<ButtonSpinner size={mockSize} color={mockColor} />);
    
    expect(ActivityIndicator).toHaveBeenCalledWith(
      expect.objectContaining({
        size: mockSize,
        color: mockColor,
        testID: 'button-loader',
      }),
      {}
    );
  });
});